import React, { useEffect, useState } from 'react'
// import { SwapPoolTabs } from '../../components/NavigationTabs'
import { Wrapper } from '../../components/swap/styleds'
import { AutoColumn } from '../../components/Column'
import { isMobile } from 'react-device-detect'
import { createChart, LineData, UTCTimestamp } from 'lightweight-charts'
// import { AutoRow } from '../../components/Row'
import AppBody from '../AppBody'
import styled from 'styled-components'

export const Item = styled.div`
  display: flex
  justify-content: center
  padding: .5rem
`

const Grid = styled.div`
  display: grid
  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: 30px 30px
  grid-gap: 5px
`
const chartProperties = {
  width: isMobile?300:520,
  height: isMobile?200:610,
  timeScale: {
    timeVisible: true,
    secondsVisible: true
  },
  layout: {
    backgroundColor: 'rgb(17, 17, 39)',
    textColor: 'rgba(255, 255, 255, 0.9)'
  },
  grid: {
    horzLines: {
      color: 'rgb(41, 44, 58)'
    },
    vertLines: {
      color: 'rgb(41, 44, 58)'
    }
  },
  priceScale: {
    borderColor: 'rgba(197, 203, 206, 0.8)'
  }
}

interface Chart {
  time: number
  value: number
}

export default function Chart() {
  const FAST_INTERVAL = 10000
  const [data, setData] = useState<Chart[] | null>([])
  const [fast, setFast] = useState(0)
  console.log(fast)

  useEffect(() => {
    async function getData() {
      const response = await fetch('https://api.moonfarm.in/api/chart')
      const responseData: Chart[] = await response.json()
       console.log(responseData)
      setData(responseData)
    }
    getData()
    const interval = setInterval(async () => {
      setFast(prev => prev + 1)
      getData()
    }, FAST_INTERVAL)
    return () => clearInterval(interval)
  }, [setData])

  const renderTable = () => {
    
    const domElement: HTMLElement = document.getElementById('tvchart')!
    if (domElement === undefined || domElement === null || domElement.childElementCount >= 1) {
      return
    } else {
      console.log(domElement)
      console.log(domElement.childElementCount)
      const chart = createChart(domElement, chartProperties)
      const lineSeries = chart.addLineSeries()
      console.log(data)
      if (data != null) {
        console.log(data)
        const cdata: LineData[] = data.map(dat => {
          return { time: dat.time as UTCTimestamp, value: dat.value }
        })
        lineSeries.setData(cdata)
        return (
          <>
 
          </>
        )
      } else {
        return (
          <>
          </>
        )
      }
    }
  }

  return (
    <>
      <AutoColumn justify="center">
        <h1>Chart (preview feature)</h1>
      </AutoColumn>
      <AppBody>
        <Wrapper id="swap-page">{renderTable()}</Wrapper>
        <Grid>
          <Item>MSWAP - USD</Item>
        </Grid>
        <div id="tvchart"></div>
        <Item>
          Grow with us! If you want to list your token &nbsp;{' '}
          <a style={{ color: 'red', textDecoration: 'none' }} href="mailto:listing@multiswap.io" target="_blank">
            {' '}
            here
          </a>
        </Item>
      </AppBody>
    </>
  )
}

